.Herosection {
  width: 1300px;
  height: 633.76px;
  margin: 39px 30px 0 40px;
}

.Herosection-content {
  background: url("hero-background.png");
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 50px;

  width: 1400px;
  height: 633.76px;
  margin: 0 20px 0 20px;
}

.content-text {
  width: 490px;
  height: 257px;
  padding: 50px 0 0 92px;
}

.content-text h1 {
  opacity: 0;

  color: #110229;
  font-family: var(--font-base);
  font-size: 56px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.content-text p {
  scale: 0;

  color: #585981;
  font-family: var(--font-base);
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 36px;
}

.Herosection-searchbar {
  width: 918px;
  height: 141px;
  flex-shrink: 0;

  margin: 101px 0 0 92px;
}

.searchbar-searchtab {
  display: flex;
  flex-direction: row;
}

.searchtab {
  width: 102px;
  height: 39px;
  padding: 10px 0 10px 0;
  border: 0;
  border-radius: 15px 15px 0px 0px;

  color: #8f90a6;
  font-family: var(--font-base);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  text-transform: uppercase;

  text-align: center;
  margin: 0;
}

.active {
  width: 102px;
  height: 39px;
  padding: 10px 0 10px 0;
  border-radius: 15px 15px 0px 0px;
  border: 0;
  color: #110229;
  font-family: var(--font-base);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  text-transform: uppercase;

  text-align: center;
  margin: 0;
  cursor: pointer;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown:hover .dropbtn {
  background-color: #fff;
  border: 0;
}

.dropdown:hover .dropdown-content {
  display: block;
}
