.Navbar {
  display: inline-flex;
  align-items: center;
}

.Navbar-un {
  list-style: none;

  display: flex;
  flex-direction: row;
  text-align: center;
  justify-content: center;
  gap: 3rem;
}

.Navbar-list {
  font-family: var(--font-base);
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 36px;
  letter-spacing: 3.2px;
  text-transform: uppercase;

  text-align: center;
  padding-top: 7px;
}

.Navbar-list a {
  text-decoration: none;
  color: #110229;
}

.login-button {
  color: #1daeff;
  font-family: var(--font-base);
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 36px;
  letter-spacing: 3.2px;
  text-transform: uppercase;
  cursor: pointer;

  width: 150px;
  height: 50px;
  color: #1daeff;

  border-radius: 14px;
  border: 2px solid #1daeff;
  background: #fff;
}
