.Section2 {
  margin-top: 103.24px;
}

.section2-text {
  color: #110229;
  text-align: center;
  font-family: var(--font-base);
  font-size: 38px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;

  width: 478px;
  height: 100px;

  margin: 0 500px 0 500px;
}

.rectangle {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  margin-top: 56px;
}

.america {
  width: 278px;
  height: 426px;

  border-radius: 30px;
  background: url("./rectangle9.png"), lightgray 50% / cover no-repeat;
}

.america > p {
  color: #110229;
  text-align: center;
  font-family: var(--font-base);
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 4.8px;
  text-transform: uppercase;

  padding-top: 30px;
}

