.Section4 {
  margin: 120px 0 0 110px;
  width: 1300px;
  height: 482px;

  border-radius: 50px;
  background: linear-gradient(180deg, #c3dfed 0%, #dff0f7 100%);

  display: flex;
  justify-content: space-between;
}

.section4-text {
  padding: 85px 0 0 86px;
}

.section4-text h1 {
  color: #110229;
  font-family: var(--font-base);
  font-size: 54px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;

  height: 160px;
  width: 364px;
  margin: 0;
}

.section4-text p {
  color: #585981;
  font-family: var(--font-base);
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 36px;

  width: 470px;
  height: 72px;
  margin: 0 0 44px 0;
}

.section4-text button {
  color: #fff;
  font-family: var(--font-base);
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 36px;
  letter-spacing: 3.2px;
  text-transform: uppercase;
  cursor: pointer;

  width: 213px;
  height: 50px;
  flex-shrink: 0;

  border-radius: 14px;
  border: 0;
  background: #1daeff;
}

.section4-image img {
  border-radius: 50px;
}
