.card {
  width: 550px;
  height: 249px;
  margin: 10px 0 10px 0;

  display: flex;

  border-radius: 30px;
  border: 1px solid #1daeff;
  background: #fff;
  box-shadow: 0px 17px 50px 0px rgba(143, 144, 166, 0.32);
}

.card1-image {
  margin: 29px 0 29px 36px;
  width: 158px;
  height: 191px;

  border-radius: 20px;
}

.card-text {
  margin: 29px 0 0 36px;
}

.card-maintext {
  color: #110229;
  font-family: var(--font-base);
  font-size: 28px;
  font-style: normal;
  font-weight: 600;

  margin: 0;
}

.card-subtext {
  color: #8f90a6;
  font-family: var(--font-base);
  font-size: 19px;
  font-style: normal;
  font-weight: 500;

  display: flex;
  justify-content: space-between;
}

.card-footertext {
  display: flex;
  justify-content: space-between;
}

.card-footertext p {
  color: #8f90a6;
  font-family: var(--font-base);
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
}

.card-footertext button {
  color: #fff;
  text-align: center;
  font-family: var(--font-base);
  font-size: 19px;
  font-style: normal;
  font-weight: 500;
  cursor: pointer;

  width: 108px;
  height: 41px;

  border-radius: 10px;
  background: #8f90a6;
}
