.Section3 {
  margin-top: 100px;

  width: 1440px;
  height: auto;
}

.section3-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  margin: 0 100px 0 150px;
}

.section3-text1 {
  color: #110229;
  font-family: var(--font-base);
  font-size: 38px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.section3-p1 {
  color: #1daeff;
  font-family: var(--font-base);
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.card-list {
  margin: 36px 100px 0 150px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
