.Searchtypes {
  width: 918px;
  height: 102px;
  flex-shrink: 0;

  border-radius: 0px 20px 20px 20px;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0px 30px 60px -15px rgba(143, 144, 188, 0.15);
  backdrop-filter: blur(10px);
}

.types {
  display: flex;
  flex-direction: row;
}

.location {
  text-align: center;
  justify-content: center;

  margin: 20px 0 20px 29px;
}

.location h2 {
  color: #110229;
  font-family: var(--font-base);
  font-size: 18px;
  font-style: normal;
  font-weight: 600;

  height: 36px;
  width: 76px;
  margin: 0;
}

.location input {
  color: #8f90a6;
  font-family: var(--font-base);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;

  border: 2px solid white;
  border-radius: 10px;
}

.location svg {
  width: 12px;
  height: 14.667px;

  margin: 0 0 0 9px;
}

.line {
  width: 2px;
  height: 46px;
  margin: 32px 0 24px 20px;

  background: #dcdceb;
}

.property {
  text-align: center;
  justify-content: center;

  margin: 20px 0 20px 35px;
}

.property h2 {
  color: #110229;
  font-family: var(--font-base);
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 36px;

  height: 36px;
  width: 123px;
  margin: 0;
}

.property input {
  color: #8f90a6;
  font-family: var(--font-base);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;

  border: 2px solid white;
  border-radius: 10px;

  width: 200px;
}

.property svg {
  width: 12px;
  height: 6px;

  margin: 0 0 0 15px;
}

.pricerange {
  text-align: center;
  justify-content: center;

  margin: 20px 0 20px 35px;
}

.pricerange h2 {
  color: #110229;
  font-family: var(--font-base);
  font-size: 18px;
  font-style: normal;
  font-weight: 600;

  height: 36px;
  width: 123px;
  margin: 0;
}

.pricerange input {
  color: #8f90a6;
  font-family: var(--font-base);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;

  border: 2px solid white;
  border-radius: 10px;
}

.searchbutton {
  margin: 20px 0 0 50px;
}

.searchbutton button {
  height: 54px;
  width: 54px;
  background-image: url("./search-button.jpg");

  border-radius: 15px;
  border: 0;
  cursor: pointer;
}
