.Footer {
  width: 1200px;
  height: 313px;
  display: flex;

  margin: 88px 0 0 150px;
}

.footer-sec1 p {
  width: 410px;

  color: #8f90a6;
  font-family: var(--font-base);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 31px;
}

.footer-social {
  display: flex;
  flex-direction: row;

  margin-top: 34px;
}

.social-facebook {
  margin-right: 28px;
}

