.sec4-email input {
  width: 227px;
  height: 54px;
  border-radius: 15px;
  border: 2px solid #d0d0e3;
  background: #fff;
  padding-left: 15px;

  color: #8f90a6;
  font-family: var(--font-base);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 23px;
}

.sec4-email button {
  width: 34px;
  height: 34px;
  position: relative;
  right: 40px;

  border-radius: 20px 20px;
  border: 0;
  background-color: #1daeff;
  cursor: pointer;
}
